<template>
  <div>
    <div v-if="formData.isAuthorization || status==0">
      <el-form ref="wechatForm" :rules="rules" :model="formData"  label-width="210px" 
        style="margin-top:20px">
        <el-form-item label="是否开启" prop="isEnable">
          <cms-switch v-model="formData.isEnable" ></cms-switch>
          <div class="gray">开启后，可使用微信快捷登录商城</div>
        </el-form-item>
        <el-form-item v-if="status != 0" label="AppId" >
          <span>{{formData.appId}}</span>
        </el-form-item>
        <el-form-item v-if="status == 0" label="AppId"  prop="appId">
          <el-input v-model="formData.appId" :autofocus="true" ref="first" maxlength="50"></el-input>
          <div class="notes">点击获取<a href="https://open.weixin.qq.com/" target="_blank">应用</a></div>
        </el-form-item>
        <el-form-item v-if="status == 0" label="AppSecrect"  prop="appKey">
          <el-input v-model="formData.appKey" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="formData.reMark" type="textarea" :rows="4" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="subData(formData)"  v-color >保存</cms-button>
      </div>
    </div>
    <div v-else>
      <div class="t-authorize" v-if="status == 1">
        <div class="t-14">开启微信公众号快捷登录，需要授权绑定已认证的微信公众号！</div>
        <div class="cms-form-footer">
          <cms-button @click="openPublic('/#/wechatPublic/list','_blank','微信公众号')"  v-color >授权公众号</cms-button>
        </div>
      </div>
      <div class="t-authorize" v-if="status == 2">
        <div class="t-14">开启小程序快捷登录，需要授权绑定已认证的小程序！</div>
        <div class="cms-form-footer">
          <cms-button  @click="openPublic('#/wechatPublic/list?activeStatus=2&type=list','_blank','微信小程序')" v-color >授权小程序</cms-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import { thirdLoginUpdate } from '@/api/setting/thirdLogin.js'
export default {
  props: {
    data: {// 当前登录平台信息
      type: [String, Object],
      default: {}
    },
    status: {// 登录平台
      type: [String, Number],
      default: 0
    }
  },
  watch: {
    status: {
      handler(newData, oldData) {
        this.formData = Object.assign({}, this.data)
      },
      immediate: true,
      deep: true
    }
  },
  data() {
    return {
      names: [{
        name: '网站应用',
        label: '适用于PC端网站扫码登录',
        info: '开启后，PC端网站可使用扫码快捷登录'
      }, {
        name: '微信公众号',
        label: '适用于微信客户端H5快捷登录',
        info: '开启后，可使用微信客户端H5快捷登录'
      }, {
        name: '小程序',
        label: '适用于微信小程序快捷登录',
        info: '开启后，可使用微信小程序快捷登录'
      }],
      rules: {
        'appId': [rules.required('请填写应用ID')],
        'appKey': [rules.required('请填写应用密钥'), rules.english()],
        'isEnable': [rules.required()]
      },
      formData: {
        'isAuthorization': 'false',
        'appId': '',
        'appKey': '',
        'isEnable': true,
        'reMark': ''
      },
      baseUrl: window.location.protocol + window.location.host
    }
  },
  methods: {
    openBox(name) { // 打开弹窗
      this.$alert('请在新窗口中完成' + name + '授权', '系统提示', {
        confirmButtonText: '刷新授权结果',
        showClose: false,
        callback: action => {
          this.$emit('refreshData')
        }
      })
    },
    subData(params) { // 保存表单信息(管理所有提交)
      this.$refs['wechatForm'].validate((valid) => {
        if (valid) {
          thirdLoginUpdate(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('保存成功')
            } else {
              this.$errorMessage('保存失败')
            }
          })
        }
      })
    },
    openPublic(url, name, title) {
      this.openBox(title)
      window.open(url, name)
    }
  },
  created() {

  }
}
</script>
<style lang="scss" scoped>
  .el-form-item .detail{
    color: #666666;
    font-size: 13px;
  }
  .notes_title{
    color: #999999;
  }
  .notes{
    color: #666666;
  }
  .notes a {
    color: #169BD5;
  }
  .we-h{
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #444444;
    margin-left: 15px;
  }
  .t-14{
    font-size: 14px;
  }
  .t-authorize{
    padding: 80px 30px;
    width: 600px;
    text-align: center;
  }
</style>



