<template>
<keep-alive> 
  <div >
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
       <li> 设置系统使用的第三方快捷登录方式</li>
       <li> 可开启/关闭第三方快捷登录方式</li>
      </cms-prompt>
      <keep-alive> 
      <el-tabs v-model="activeName" style="margin-top:20px" >
        <el-tab-pane label="QQ登录" name="QQ">
           <el-form ref="qqForm" :rules="rules" :model="formData.QQ"  label-width="210px" style="margin-top:20px">
            <el-form-item label="是否开启QQ登录" prop="isEnable">
              <cms-switch v-model="formData.QQ.isEnable" ></cms-switch>
              <div class="gray">开启后，可使用QQ快捷登录商城</div>
            </el-form-item>
            <el-form-item label="应用ID" :error='errorMessage.appId' prop="appId">
              <el-input v-model="formData.QQ.appId" :autofocus="true" ref="first" maxlength="50"></el-input>
              <div class="notes">点击获取<a href="https://connect.qq.com/" target="_blank">应用</a></div>
            </el-form-item>
            <el-form-item label="应用密钥" :error='errorMessage.appKey' prop="appKey">
              <el-input v-model="formData.QQ.appKey" :autofocus="true" maxlength="100"></el-input>
            </el-form-item>
            <el-form-item label="备注" >
              <el-input v-model="formData.QQ.reMark" type="textarea" :rows="6" maxlength="200"></el-input>
            </el-form-item>
          </el-form>
          <div class="cms-form-footer">
            <cms-button  @click="saveData('QQ')"  v-color >保存</cms-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="微信" name="WECHAT">
            <el-tabs tab-position="left" v-model="weActive" class="we-menu" @tab-click="handleOpen">
              <el-tab-pane name="0">
                <div slot="label" class="we-menu-name">
                  <el-popover
                    placement="right-start"
                    width="220"
                    transition="fade-in-linear"
                    :visible-arrow="false"
                    trigger="hover">
                    <span>适用于PC端网站扫码登录</span>
                    <img slot="reference" src="./../../../../assets/images/third-pc.png" alt=""> 
                  </el-popover>
                  <p class="we-menu-tit">网站应用</p>
                </div>
                <div class="we-layout" v-if="weData">
                  <wechat-login @refreshData="reshData" :status="weStatus" :data="weData"></wechat-login>
                </div>
              </el-tab-pane>
              <el-tab-pane name="1">
                <div slot="label" class="we-menu-name">
                  <el-popover
                    placement="right-start"
                    width="240"
                    transition="fade-in-linear"
                    :visible-arrow="false"
                    trigger="hover">
                    <span>适用于微信客户端H5快捷登录</span>
                    <img slot="reference" src="./../../../../assets/images/third-h5.png" alt=""> 
                  </el-popover>
                  <p class="we-menu-tit">微信公众号</p>
                </div>
                <div class="we-layout" v-if="weData">
                  <wechat-login @refreshData="reshData" :status="weStatus" :data="weData"></wechat-login>
                </div>
              </el-tab-pane>
              <el-tab-pane name="2">
                <div slot="label" class="we-menu-name">
                  <el-popover
                    placement="right-start"
                    width="220"
                    transition="fade-in-linear"
                    :visible-arrow="false"
                    trigger="hover">
                    <span>适用于微信小程序快捷登录</span>
                    <img slot="reference" src="./../../../../assets/images/third-wechat.png" alt=""> 
                  </el-popover>
                  <p class="we-menu-tit">小程序</p>
                </div>
                <div class="we-layout" v-if="weData">
                  <wechat-login @refreshData="reshData" :status="weStatus" :data="weData"></wechat-login>
                </div>
              </el-tab-pane>
            </el-tabs>
        </el-tab-pane>
        <el-tab-pane label="微博" name="WEIBO">
          <el-form ref="blogForm" :rules="rules" :model="formData.WEIBO"  label-width="210px" style="margin-top:20px">
            <el-form-item label="是否开启微博登录" prop="isEnable">
              <cms-switch v-model="formData.WEIBO.isEnable"></cms-switch>
              <div class="gray">开启后，可使用微博快捷登录商城</div>
            </el-form-item>
            <el-form-item label="应用ID" :error='errorMessage.appId' prop="appId">
              <el-input v-model="formData.WEIBO.appId" :autofocus="true" ref="first" maxlength="50"></el-input>
              <div class="notes">点击获取<a href="http://open.weibo.com/index.php" target="_blank">应用</a></div>
            </el-form-item>
            <el-form-item label="应用密钥" :error='errorMessage.appKey' prop="appKey">
              <el-input v-model="formData.WEIBO.appKey" :autofocus="true" maxlength="100"></el-input>
            </el-form-item>
            <el-form-item label="备注" >
              <el-input v-model="formData.WEIBO.reMark" type="textarea" :rows="6" maxlength="200"></el-input>
            </el-form-item>
          </el-form>
          <div class="cms-form-footer">
            <cms-button  @click="saveData('WEIBO')"  v-color >保存</cms-button>
          </div>
        </el-tab-pane>
      </el-tabs>
      </keep-alive>
    </div>
  </div>
  </keep-alive>
</template>
<script>
import axios from 'axios'
import rules from '@/rules'
import form from '@/mixins/form.js'
import wechatLogin from './wechatLogin'
import { thirdLoginGet, thirdLoginUpdate } from '@/api/setting/thirdLogin.js'
var defaultData = {
  QQ: {}, // QQ
  WECHAT_WEB: {}, // 网站
  WECHAT_H5: {}, // 公众号
  WECHAT_MINIPROGRAM: {}, // 小程序
  WEIBO: {} // 微博
}
export default {
  name: 'thirdLoginList',
  mixins: [form],
  components: {
    wechatLogin
  },
  data() {
    return {
      weStatus: 0,
      weActive: '0',
      weData: null,
      formData: Object.assign({}, defaultData),
      activeName: 'QQ',
      rules: {
        'appId': [rules.required('请填写应用ID')],
        'appKey': [rules.required('请填写应用密钥'), rules.english()],
        'isEnable': [rules.required()]
      }
    }
  },
  methods: {
    reshData() { // 授权后数据刷新
      this.getFormData()
      this.editData()
    },
    editData() {
      switch (this.weStatus) {
        case 0:
          this.weData = this.formData.WECHAT_WEB
          break
        case 1:
          this.weData = this.formData.WECHAT_H5
          break
        case 2:
          this.weData = this.formData.WECHAT_MINIPROGRAM
          break
      }
    },
    handleOpen(tab) { // 微信相关tab切换
      this.weStatus = parseInt(tab.index)
      this.editData()
    },
    getFormData() { // 一次性获取所有的表单信息
      axios
        .all([
          thirdLoginGet({ code: 'QQ' }),
          thirdLoginGet({ code: 'WECHAT_WEB' }),
          thirdLoginGet({ code: 'WECHAT_H5' }),
          thirdLoginGet({ code: 'WECHAT_MINIPROGRAM' }),
          thirdLoginGet({ code: 'WEIBO' })
        ])
        .then(
          axios.spread(
            (QQ, WECHAT_WEB, WECHAT_H5, WECHAT_MINIPROGRAM, WEIBO) => {
              this.formData.QQ = QQ.data
              this.formData.WECHAT_WEB = WECHAT_WEB.data
              this.formData.WECHAT_H5 = WECHAT_H5.data
              this.formData.WECHAT_MINIPROGRAM = WECHAT_MINIPROGRAM.data
              this.formData.WEIBO = WEIBO.data
              this.editData()
            }
          )
        )
    },
    saveData(type) { // 保存表单信息(管理所有提交)
      if (this.activeName === 'QQ') {
        this.formData.QQ.code = 'QQ'// 配置后台所需的参数
        this.submitForm('qqForm', this.formData.QQ)
      } else if (this.activeName === 'WECHAT') {
        return
      } else {
        this.formData.WEIBO.code = 'WEIBO'
        this.submitForm('blogForm', this.formData.WEIBO)
      }
    },
    submitForm(formName, params) { // 提交表单(QQ,微博)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          thirdLoginUpdate(params)
            .then(res => {
              if (res.code === 200) {
                this.$successMessage('保存成功')
              } else {
                this.$errorMessage('保存失败')
              }
            })
        } else {
          return false
        }
      })
    }
  },
  created() {
    this.getFormData()
  }
}

</script>
<style>
.el-form-item .detail{
  color: #666666;
  font-size: 13px;
}
.notes_title{
  color: #999999;
}
.notes{
  color: #666666;
}
.notes a {
  color: #169BD5;
}
.we-menu{
  height: 600px;
  position: relative;
  z-index: 10;
}
.we-menu .el-tabs__item{
  height: 80px;
  margin: 0;
}
.we-menu-name{
  position: relative;
  z-index: 10;
  height: 80px;
  padding-top: 15px;
  text-align: center;
  cursor: pointer;
}
.we-menu-tit{
  color: #353535;
  margin: 0;
  font-size: 12px;
  font-weight: normal;
  letter-spacing: 2px;
  line-height: 14px;
}
.we-layout{
  padding-left: 50px;
}
</style>



