// 第三方登录相关接口
import axios from './../fetch'

// 查询出一个第三方登录设置的信息
export const thirdLoginGet = params => { return axios.get('/admin/platform/third/get', { params: params }) }

// 修改一个地三方登录设置
export const thirdLoginUpdate = params => { return axios.post('/admin/platform/third/update', params) }



